import React, { Component } from "react";
import Zmage from "react-zmage";
import Fade from "react-reveal";
import './portifolio.css'
let id = 0;
class Portfolio extends Component {
  render() {
    if (!this.props.data) return null;

    const projects = this.props.data.projects.map(function (projects) {
      let projectImage = "images/portfolio/" + projects.image;

      return (
        <div key={id++} className="columns portfolio-item">
          <div className="item-wrap">
            
          <div class="flip-card">
  <div class="flip-card-inner">
    <div class="flip-card-front">
     
     
    <Zmage  alt={projects.title} src={projectImage} />
            <div  style={{ textAlign: "center",fontSize:20 ,color:'white'}}>{projects.title}
            <div style={{ fontSize:15,color:'grey' }}>
            {/* <a style={{ color:'White' }} target="_blank" href={projects.url}>{projects.desc}</a> */}
            {projects.desc}
            </div>
            
            </div>
      {/* <img src="img_avatar.png" alt="Avatar" style="width:300px;height:300px;"> */}
    </div>
    <div class="flip-card-back">
    <a  target="_blank" style={{marginTop: "200px"}} href={projects.url} className="button btn github-btn">
                  <i className="fa fa-github"></i>Github
                </a>
    </div>
  </div>
</div>

          </div>
        </div>
      );
    });

    return (
      <section id="portfolio">
        <Fade left duration={1000} distance="40px">
          <div className="row">
            <div className="twelve columns collapsed">
              <h1 className="jump">Check Out Some of My Works.</h1>

              <div
                id="portfolio-wrapper"
                className="bgrid-quarters s-bgrid-thirds cf"
              >
                {projects}
              </div>
            </div>
          </div>
        </Fade>
      </section>
    );
  }
}

export default Portfolio;
